import * as React from 'react';
import { graphql } from 'gatsby';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
//import { IndexHero } from '~src/components/Layout/Header/Hero';
import Layout from '~src/components/Layout/Layout';
import { VFXHero } from '~src/components/Landing/VFXHero/VFXHero';
import { PostFeed } from '~src/components/Sections/Posts/PostFeed/PostFeed';
import { FilmFeed } from '~src/components/Sections/Filmography/FilmFeed/FilmFeed';
import { Index as IndexData } from '~src/pages/__generated__/Index';

const VFXPage: React.FC<{ data: IndexData }> = ({ data }) => {
	const stickyRef: React.RefObject<HTMLDivElement> = React.useRef(null);
	const { formatMessage } = useIntlHelper();
	return (
		<>
			<Layout template="home" stickyRef={stickyRef} header={<VFXHero />}>
				<div className="inner" ref={stickyRef}>
					<h2> {formatMessage('navigation.projects')}</h2>
					<FilmFeed data={data.projects} isHome />

					<h2> {formatMessage('navigation.news')}</h2>

					<PostFeed data={data} isHome />
				</div>
			</Layout>
		</>
	);
};
export default VFXPage;

export const VFXPageQuery = graphql`
	query VFXPage($locale: String!) {
		projects: allMdx(
			sort: { fields: [frontmatter___featureOrder, frontmatter___release], order: [ASC, DESC] }
			filter: { fields: { locale: { eq: $locale }, category: { eq: "filmography" } }, frontmatter: { tags: { in: "vfx" } } }
		) {
			nodes {
				fields {
					slug
					category
				}
				frontmatter {
					title
					director
					release(formatString: "DD MMMM YYYY")
					tags
					imdb
					image {
						childImageSharp {
							gatsbyImageData(layout: FULL_WIDTH, placeholder: TRACED_SVG)
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 4
			filter: { fields: { locale: { eq: $locale }, category: { eq: "posts" } }, frontmatter: { tags: { in: "vfx" }, draft: { ne: true } } }
		) {
			nodes {
				fields {
					slug
					category
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					featured
					tags
					image {
						childImageSharp {
							gatsbyImageData(layout: FULL_WIDTH, placeholder: TRACED_SVG)
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
